@import url("https://fonts.googleapis.com/css?family=Quattrocento+Sans:400,700");
@import url("https://fonts.googleapis.com/css?family=Lovers+Quarrel");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

body {
  font-family: "Quattrocento Sans", sans-serif; }

h1 {
  font-size: 16rem; }

h2 {
  font-size: 13rem; }

h3 {
  font-size: 10rem; }

h4 {
  font-size: 7rem; }

a,
a:visited {
  color: #342610;
  text-decoration: none;
  transition: 250ms cubic-bezier(0.075, 0.82, 0.165, 1) all; }
  a:hover,
  a:visited:hover {
    color: #c2cad1; }

p {
  font-size: 1.75rem;
  line-height: 1.75;
  color: #FDFBF9; }
  p:not(:last-child) {
    margin-bottom: .8rem; }
  p.editor-rich-text__tinymce {
    background-color: #471500; }

#page {
  background-position: center;
  background-size: cover;
  background-image: url(assets/BG-Parchment.jpg);
  background-repeat: no-repeat;
  padding-bottom: 10rem; }

.container {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto; }

