.site-header {
  position: fixed;
  height: 7rem;
  width: 100%;
  background-color: #471500;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  opacity: 0;
  transition: opacity 1200ms cubic-bezier(0.36, 0.76, 0.72, 1);
  z-index: 10; }
  .site-header.fade-in {
    opacity: 1; }
  .site-header ul {
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
  .site-header li {
    font-size: 3rem;
    font-weight: 400;
    list-style: none;
    margin-right: 2rem; }
  .site-header a {
    color: #FDFBF9; }
    .site-header a.site-header__logo {
      color: #fff; }
  .site-header__logo {
    margin: 0 2%;
    height: 80%;
    width: 15rem;
    background-position: center;
    background-size: cover;
    line-height: 5.6rem;
    font-size: 5.6rem;
    font-family: "Lovers Quarrel", cursive; }

body.home .hero-homepage {
  height: 100vh;
  display: flex;
  justify-content: center;
  z-index: 0;
  background-color: #FDFBF9; }
  body.home .hero-homepage__text {
    align-self: center; }
    body.home .hero-homepage__text--title {
      align-self: center;
      text-align: center;
      font-family: "Lovers Quarrel", cursive;
      font-weight: unset;
      color: #471500;
      z-index: 2; }
    body.home .hero-homepage__text--tagline {
      align-self: center;
      text-align: center;
      font-family: "Quattrocento Sans", sans-serif;
      font-size: 3rem;
      font-weight: unset;
      color: #471500;
      opacity: 0;
      transition: opacity 1200ms cubic-bezier(0.36, 0.76, 0.72, 1); }
      body.home .hero-homepage__text--tagline.fade-in {
        opacity: 1; }

body.home .intro {
  position: relative;
  overflow: hidden; }
  body.home .intro__video-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(79, 112, 165, 0.4);
    display: flex;
    justify-content: center; }
    body.home .intro__video-overlay--text {
      color: #FDFBF9;
      font-size: 4rem;
      max-width: 50%;
      word-break: break-word;
      text-align: center;
      margin: auto 0; }
  body.home .intro__video {
    object-fit: cover;
    height: calc(100vh - 7rem);
    width: 100%; }

body.home .project {
  display: flex;
  height: calc(50vh - 3.5rem);
  min-height: calc(50vh - 3.5rem); }
  body.home .project.odd {
    background: #FDFBF9; }
  body.home .project.even {
    background: #c2cad1; }
  body.home .project .container {
    display: flex; }
    body.home .project .container.odd {
      flex-direction: row;
      color: #174F41; }
    body.home .project .container.even {
      flex-direction: row-reverse;
      color: #FDFBF9; }
  body.home .project__image {
    object-fit: cover;
    height: calc(50vh - 3.5rem);
    width: 50%;
    mask-image: linear-gradient(to left, black, rgba(0, 0, 0, 0)); }
  body.home .project__title {
    color: inherit;
    font-size: 5rem;
    font-weight: 600; }
    body.home .project__title.link {
      transition: 250ms cubic-bezier(0.075, 0.82, 0.165, 1) all; }
      body.home .project__title.link.odd:hover {
        color: #c2cad1; }
      body.home .project__title.link.even:hover {
        color: #174F41; }
  body.home .project__description {
    color: inherit; }
